.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 10px;
  border: 1px solid #686868;
  border-radius: 10px;
  padding: 20px 40px;
  margin-bottom: 10px;
}

.text {
  font-size: 20px;
}

.radio {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.r1,
.r2 {
  display: flex;
  align-items: center;
}

.input {
  margin-right: 5px;
}
